body {
 max-width: $body-max-width;
}

header {
 background-repeat: no-repeat;
 box-shadow: $box-shadow !important;
 position: absolute;
 top: 0;
 z-index: 100;
}

nav {
 background-color: $nav-bg;
 -webkit-backdrop-filter: $nav-bg-filter;
 backdrop-filter: $nav-bg-filter;
 
 &.navbar {
 border-top: 1px solid rgba($white,0.45);
 border-bottom: 1px solid rgba($white,0.45);
 position: fixed;
 left: 0;
 right: 0;
 top: 0;
 padding: 0;
 transition-timing-function: ease;
 transition-duration: .5s;
 transition-property: padding, box-shadow;
 z-index: 1030;
 }
 
 .nav-link {
 border-top: 1px solid rgba($black,0.05);
 color: $navbar-color;
 
 @include hover-focus() {
 color: $navbar-hover-color;
 }
 
 text-align: center; 
 }
 
 .navbar-toggler {
 @include caret('down');
 font-weight: bold;
 color: $black !important;
 padding: $spacer*.75 0;
 width: 100%;
 }
 
 .navbar-toggler[aria-expanded="true"], .dropdown-toggle[aria-expanded="true"] {
 @include caret('up');
 }
 
 .dropdown-menu {
 border-color: $white;
 margin: 0 1rem;
 transition-timing-function: ease;
 transition-duration: .5s;
 transition-property: height, margin;

 a:first-child {
 margin-top: 2*$spacer/3;
 }

 a:last-child {
 margin-bottom: 2*$spacer/3;
 }

 &.collapsing {
 display:block;
 }
 
 &.collapsing, &.show {
 margin-bottom: 1rem !important;
 }
 }
}

section {
 z-index: 1;
}

h2 {
 font-size: $h2-font-size;
}

h3 {
 font-size: $h3-font-size;
 padding: $h3-p;
}

.icon-email {
 margin-top: $icon-email-mt;
 width: $icon-email-w;
}

.prg {
 .prg-row {
 opacity: $prg-row-o;
 transition-timing-function: ease;
 transition-duration: .5s;
 transition-property: opacity;
 
 @include hover-focus() {
 opacity: 1;
 }
 }
 
 .prg-timing {
 font-weight: bold;
 }
 
 .prg-icon-timing {
 margin-top: -.1rem;
 width: $prg-icon-timing-w;
 }
 
 .prg-icon-large {
 width: $prg-icon-large-w;
 }
 
 .prg-inner-icon {
 margin-right: .2rem;
 }
}

footer {
 padding-bottom: 5rem;
}

@supports (-webkit-touch-callout: none) {
 nav {
 .navbar-toggler {
 margin-bottom: 1rem;
 margin-top: 1rem;
 }
 }
 
 footer {
 padding-bottom: 8rem;
 }
}

@include media-breakpoint-up(sm) {
 header {
 box-shadow: none !important;
 }

 nav {
 background-color: $nav-bg-sm;

 .navbar-toggler {
 margin-bottom: 0rem;
 margin-top: 0rem;
 padding: $spacer*.75 0;
 }
 
 
 &.navbar {
 box-shadow: $box-shadow !important;
 position: relative;
 }
 }
}

@include media-breakpoint-up(md) {
 nav {
 &.navbar {
 padding: $spacer/2 $spacer;
 }

 .nav-link {
 border-top: 0;
 }
 
 .dropdown-menu {
 margin: 0;
 }
 }

 footer {
 padding-bottom: 3rem;
 }
}

@each $breakpoint in map-keys($grid-breakpoints) {
 @include media-breakpoint-up($breakpoint) {
 header {
 background-image: map-get($header-bg-imgs, $breakpoint);
 min-height: map-get($header-logo-hs, $breakpoint);
 transition-timing-function: ease;
 transition-duration: .5s;
 transition-property: min-height;
 
 .logo {
 height: map-get($header-logo-hs, $breakpoint) - $nav-height;
 width: map-get($header-logo-ws, $breakpoint);
 @include transition(width)
 transition-timing-function: ease;
 transition-duration: .5s;
 transition-property: width, height;
 }
 }

 section {
 margin-top: map-get($header-logo-hs, $breakpoint)/2;;
 }
 }
}


