
$primary: #A92B7A;
$body-max-width: 2000px;

$nav-bg: rgba($white, 0.2);
$nav-bg-sm: rgba($white, 0.5);
$nav-bg-fx: rgba($white, 0.95);
$nav-bg-filter: saturate(180%) blur(20px);
$nav-height: 56px;

$header-bg-imgs: (
 xs: url(../img/masthead-xs.png),
 sm: url(../img/masthead-sm.png),
 md: url(../img/masthead-md.png),
 lg: url(../img/masthead-lg.png),
 xl: url(../img/masthead-xl.png)
);

$header-logo-ws: (
 xs: 270px,
 sm: 225px,
 md: 300px,
 lg: 380px,
 xl: 590px
);
$header-logo-hs: (
 xs: 300px,
 sm: 285px,
 md: 350px,
 lg: 420px,
 xl: 480px
);


$navbar-color: $black;
$navbar-hover-color: $black;

$h2-font-size: 1.6rem;

$h3-font-size: 1.2rem;
$h3-p: $spacer/2 0;

$icon-email-w: 1.5rem;
$icon-email-mt: -2px;

$prg-row-o: .75;
$prg-icon-large-w: $icon-email-w;
$prg-icon-timing-w: $icon-email-w/1.5;